'use client';

import React from 'react';
import Image from 'next/image';
import { useTheme } from 'next-themes';
import '../../styles/CertificateCard.module.css'; // 👈 Asegúrate de importar los estilos


interface CertificateCardProps {
  title: string;
  subtitle: string;
  status: 'locked' | 'completed';
  image: string;
  lockedImage: string;
  titleLocked: string;
  titleUnlocked: string;
  descriptionLocked: string;
  descriptionUnlocked: string;
  shareUrl: string;
}

const CertificateCard: React.FC<CertificateCardProps> = ({
  title,
  subtitle,
  status,
  image,
  lockedImage,
  titleLocked,
  titleUnlocked,
  descriptionLocked,
  descriptionUnlocked,
  shareUrl,
}) => {
  const { theme } = useTheme();
  const isCompleted = status === 'completed';

  const iconStatus = `/assets/${theme === 'dark' ? 'dark' : 'light'}/${isCompleted ? 'unlocked.svg' : 'lock.svg'}`;

  const socialLinks = [
    {
      name: 'WhatsApp',
      url: `https://api.whatsapp.com/send?text=Mira mi certificado: ${shareUrl}`,
      icon: `/assets/${theme === 'dark' ? 'dark' : 'light'}/whatsapp.svg`,
    },
    {
      name: 'Facebook',
      url: `https://www.facebook.com/sharer/sharer.php?u=${shareUrl}`,
      icon: `/assets/${theme === 'dark' ? 'dark' : 'light'}/facebook.svg`,
    },
    {
      name: 'LinkedIn',
      url: `https://www.linkedin.com/sharing/share-offsite/?url=${shareUrl}`,
      icon: `/assets/${theme === 'dark' ? 'dark' : 'light'}/linkedin.svg`,
    },
  ];

  return (
    <div className={`card ${isCompleted ? 'unlocked' : 'locked'}`}>
      {/* Estado */}
      <div className="iconStatus">
        <Image src={iconStatus} alt="Estado" width={20} height={20} />
      </div>

      {/* 🔓 DESBLOQUEADO */}
      {isCompleted ? (
        <>
          <h2 className="titleLevel titleLevelUnlocked">{title}</h2>
          <p className="subtitleLevel subtitleLevelUnlocked">{subtitle}</p>

          <p className="descriptionTitle descriptionTitleUnlocked">{titleUnlocked}</p>
          <p className="descriptionText descriptionTextUnlocked">{descriptionUnlocked}</p>

          <div className="imageContainer">
            <Image
              src={image}
              alt={`Certificado ${title}`}
              width={220}
              height={160}
              className="image"
            />
          </div>

          <button className="downloadButton">Descargar Certificado</button>

          <div className="mt-4">
            <p className="text-xs font-bold text-[var(--color-neutral-600)] mb-2">Compártelo</p>
            <div className="socialContainer">
              {socialLinks.map(({ name, url, icon }) => (
                <a key={name} href={url} target="_blank" rel="noopener noreferrer">
                  <Image src={icon} alt={name} width={20} height={20} className="socialIcon" />
                </a>
              ))}
            </div>
          </div>
        </>
      ) : (
        <>
          <h2 className="titleLevel titleLevelLocked">{title}</h2>
          <p className="subtitleLevel subtitleLevelLocked">{subtitle}</p>

          <div className="imageContainer">
            <Image
              src={lockedImage}
              alt={`Imagen bloqueada ${title}`}
              width={220}
              height={160}
              className="image"
            />
          </div>

          <p className="descriptionTitle descriptionTitleLocked">{titleLocked}</p>
          <p className="descriptionText descriptionTextLocked">{descriptionLocked}</p>
        </>
      )}
    </div>
  );
};

export default CertificateCard;
